<template>
  <div>
    <h1>  </h1>
    <table>
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th> </th>
          <th></th>
          <th> </th>
          <th> </th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(stock, index) in unsoldTransactions" :key="index">
          <td>{{ stock.stock_symbol }}</td>
          <td>{{ stock.trade_date }}</td>
          <td>{{ stock.price }}</td>
          <td>{{ stock.quantity }}</td>
          <td>{{ stock.total_amount }}</td>
          <td>{{ stock.current_price !== null ? stock.current_price + '' : ' ...' }}</td>
          <td :style="getDifferenceStyle(stock)">
            {{ calculateDifference(stock) }}
          </td>
          <td><button @click="sellStock(index)"></button></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Cookies from 'js-cookie';
import axios from 'axios';

export default {
  data() {
    return {
      transactions: [] //     
    };
  },
  computed: {
    unsoldTransactions() {
      //    
      return this.transactions.filter(transaction => !transaction.sell_price);
    }
  },
  created() {
    //     
    const savedTransactions = Cookies.get('transactions');
    this.transactions = savedTransactions ? JSON.parse(savedTransactions) : [];

    //   API 
    this.fetchCurrentPrices();
  },
  methods: {
    fetchCurrentPrices() {
      this.transactions.forEach((stock) => {
        // WTI  API 
        axios.post('http://152.69.226.208:5500/wtiEtn')
          .then(response => {
            // API    
            stock.current_price = response.data.current_price; // this.$set   
          })
          .catch(error => {
            console.error('  .', error);
            stock.current_price = null; //   null 
          });
      });
    },
    calculateDifference(stock) {
      if (stock.current_price !== null) {
        return (stock.current_price - stock.price).toLocaleString(); //  
      }
      return 'N/A';
    },
    getDifferenceStyle(stock) {
      const difference = stock.current_price - stock.price;
      if (difference > 0) {
        return { color: 'red' }; //  0  
      } else if (difference < 0) {
        return { color: 'blue' }; //  0  
      }
      return {};
    },
    sellStock(index) {
      const sell_price = prompt('  ');
      const sell_date = prompt('   (YYYY-MM-DD)');

      //   
      this.transactions[index].sell_price = sell_price;
      this.transactions[index].sell_date = sell_date;

      //   
      Cookies.set('transactions', JSON.stringify(this.transactions), { expires: 36500 });

      alert('  .');
    }
  }
};
</script>
